.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.how-its-made-list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden .modal {
  transform: scale(.1);
}

.modal-open {
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

body {
  color: #131313;
  letter-spacing: .04em;
  background-color: #fcfcfc;
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

.container {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.page-footer {
  background-color: #84a178;
}

.page-footer__container {
  color: #fcfcfc;
}

.page-footer__main-part {
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .page-footer__main-part {
    padding-bottom: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__main-part {
    border-bottom: 1px solid #e1e1e1;
    padding-bottom: 60px;
  }
}

.page-footer__title {
  width: 234px;
}

@media screen and (min-width: 768px) {
  .page-footer__title {
    width: 352px;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__title {
    width: 504px;
  }
}

.page-footer__header {
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.22;
  display: block;
}

@media screen and (min-width: 768px) {
  .page-footer__header {
    margin-bottom: 8px;
    font-size: 26px;
    line-height: 1.23;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__header {
    margin-bottom: 14px;
    font-size: 30px;
    font-weight: 400;
  }
}

.page-footer__additional-header {
  text-transform: uppercase;
  font-size: 34px;
  font-weight: 700;
  line-height: 1.2;
  display: block;
}

@media screen and (max-width: 767px) {
  .page-footer__additional-header {
    margin-bottom: 60px;
  }
}

@media screen and (min-width: 768px) {
  .page-footer__additional-header {
    letter-spacing: .06em;
    font-size: 48px;
    line-height: 1.23;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__additional-header {
    font-size: 58px;
    line-height: 1.22;
  }
}

@media screen and (min-width: 768px) {
  .page-footer__contacts {
    margin-left: auto;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .page-footer__contacts {
    margin-top: 61px;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__contacts {
    margin-top: auto;
  }
}

.page-footer__mobile {
  text-align: right;
  color: #fcfcfc;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.86;
  text-decoration: none;
  display: block;
}

@media screen and (min-width: 768px) {
  .page-footer__mobile {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .page-footer__mobile {
    font-size: 16px;
  }
